import React, { useContext } from 'react'
import { Form, Input, Button } from 'antd';
import { SubmitContext } from '../../App';
import Loading from '../Loading';

const MyForm = ({ infoHandler, setStatus }) => {

  const [form] = Form.useForm();

  const {isSubmited, setIsSubmited} = useContext(SubmitContext)

  const handleSubmit = async(formData) => {
    // 
    setStatus(1)
    setIsSubmited(true)
    // fetchAPIinfoHandler
    const result = await fetch("/api/data.json")
    const data = await result.json()
    infoHandler(formData, data)
  }

  return (
    <>
      {
        isSubmited ?
        <Loading /> :
        <Form form={form} layout='vertical' style={{margin: '20px 150px', marginBottom: '15px'}} onFinish={handleSubmit}>
          <Form.Item label="" tooltip='' name='' rules={[
            {
              required: true,
              message: ''
            }
          ]}>
            <Input placeholder='' allowClear />
          </Form.Item>
          <Form.Item label="" name='' rules={[
            {
              required: true,
              message: ''
            }
          ]}>
            <Input placeholder='A123456789' allowClear />
          </Form.Item>
          <Form.Item label="" name='' rules={[
            {
              required: true,
              message: ''
            }
          ]}>
            <Input placeholder="" allowClear />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType='submit'></Button>
          </Form.Item>
        </Form>
      }
    </>
  )
}

export default MyForm
