import React, { useState } from 'react'
import { Result } from 'antd'
import Progress from '../Progress';
import MyForm from '../MyForm';
import Displayer from '../Displayer';
import './index.css';

const Main = () => {

  // 
  const [status, setStatus] = useState(0)
  // 
  const [data, setData] = useState({})

  // 
  const infoHandler = (formData, fetchedData) => {
    const {credentialTemplate: {credentialDefinition: {format}}} = fetchedData // format
    // 
    const resultArr = format.filter(obj => obj.user_write === false)
    // data(object)
    const newData = formData
    // dataobject
    resultArr.forEach(dataObj => newData[dataObj.key] = dataObj.value)
    // data
    setData(newData)
    // 
    setStatus(3)
  }

  return (
    <div className='main'>
      <Progress status={status} />
      {
        status === 3 ?
        <Result
          status="success"
          title="!"
          extra={[
            <Displayer data={data} key='displayer' />
          ]}
        /> :
        <MyForm infoHandler={infoHandler} setStatus={setStatus} />
      }
    </div>
  )
}

export default Main;