import React from 'react'
import { Steps } from 'antd'

const { Step } = Steps

const Progress = ({status}) => {
  return (
    <Steps current={status} responsive={false} style={{ maxWidth: '550px', margin: '0 auto', fontWeight: 'bold' }}>
      <Step title="" />
      <Step title="" />
      <Step title="" />
    </Steps>
  )
}

export default Progress
